:host {
  display: block;
  width: fit-content;
}
:host .file-card {
  position: relative;
  border-radius: 4px;
  border: 1px solid #e0e0e0;
  overflow: hidden;
}
:host .file-card .actions {
  position: absolute;
  top: -21px;
  right: -21px;
  z-index: 1;
  display: none;
}
:host .file-card img {
  border-radius: 4px;
}
:host .file-card:hover .actions {
  display: block;
}
:host .file-card .informations {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  padding: 0 4px;
}
:host .size-16 {
  height: 4rem;
  width: 4rem;
}
:host .size-32 {
  height: 8rem;
  width: 8rem;
}
:host .size-64 {
  height: 16rem;
  width: 16rem;
}
:host .size-128 {
  height: 32rem;
  width: 32rem;
}
:host .size-256 {
  height: 64rem;
  width: 64rem;
}
